<template>
  <tr>
    <th>{{ album }}</th>
    <th>{{ item }}</th>
    <th>{{ createSize(size) }}</th>
    <th>
      <button v-on:click="loadSong(item)">PLAY</button>
    </th>
  </tr>
</template>

<script>
export default {
  name: "listOfSongs",
  props: {
    item: String,
    size: Number,
    album: String,
  },
  methods: {
    createSize(size) {
      return (size / 1048576).toFixed(2) + " MB";
    },
    loadSong(song) {
      this.$store.dispatch("playSong", song);
    },
  },
  computed: {
    getActualDir() {
      return this.$store.getters.getActualDir;
    },
  },
};
</script>
<style>
.listOfSongs {
  width: 100px;
  height: 100px;
  float: left;
}
th {
  width: 200px;
  text-align: left;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 5px;
}
tr:hover {
  background-color: lightblue;
}
tr > th:last-child {
  display: block;
  width: 50px;
}
/* tr:hover > th:last-child {
  background-color: lightblue;
  display: block;
} */
tr {
  margin-top: 10px;
}
</style>
