<template>
  <div id="app">
    <div id="leftBox">
      <div class="albums">
        <albums
          v-for="item in getAllDirs"
          :key="item"
          :item="item"
          v-on:click.native="loadAlbum(item)"
        />
      </div>
    </div>

    <div id="middleBox">
      <table>
        <listOfSongs
          v-for="item in getAllFiles"
          :album="getActualDir"
          :key="item"
          :item="item.file"
          :size="item.size"
        />
      </table>
      <audio id="audio" controls>
        <source v-bind:src="nowPlayed" id="audio_src" type="audio/mp3" />
      </audio>
    </div>
  </div>
</template>

<script>
import listOfSongs from "./components/listOfSongs.vue";
import albums from "./components/albums.vue";

export default {
  name: "App",
  components: {
    listOfSongs,
    albums,
  },
  mounted() {},
  computed: {
    getActualDir() {
      return this.$store.getters.getActualDir;
    },
    getAllDirs() {
      return this.$store.getters.getAllDirs;
    },
    getAllFiles() {
      return this.$store.getters.getAllFiles;
    },
  },
  methods: {
    loadAlbum(name) {
      this.$store.dispatch("loadAlbum", name);
    },
  },
  created() {
    this.$store.dispatch("readFirstTime");
    console.log("Zapytanie przy ładowaniu strony");
  },
};
</script>

