import Vuex from "vuex"
import Vue from "vue"

Vue.use(Vuex);
// dirs: ["amarena"],
// actualDir: "amarena",
// files: [{ file: "awokado", size: 133377 }, { file: "2.     asdasawokado", size: 123452112 }],
//export store
export default new Vuex.Store({
    state: {
        dirs: [],
        actualDir: "",
        files: [],
        actualPlay: ""
    },
    getters: {
        getActualDir: (state) => { return state.actualDir },
        getAllDirs: (state) => { return state.dirs },
        getAllFiles: (state) => { return state.files },
    },
    actions: {
        playSong(state, nameOfSong) {
            state.actualPlay = nameOfSong
            document.getElementById("audio_src").src = "./static/albums/" + this.state.actualDir + "/" + state.actualPlay
            document.getElementById("audio").load(); // UWAGA - dopiero w tym momencie powinna być możliwość wylogowania GET-a danego pliku mp3 na serwerze
            document.getElementById("audio").play(); // graj plik mp3
        },
        async readFirstTime(state) {
            try {
                console.log(this.state.href + "/")
                const headers = { "Contet-Type": "application/json" };
                const body = JSON.stringify({ firstLoad: true, album: "" });
                const server = await fetch("/", { method: "POST", body, headers, })
                const data = await server.json()
                state.commit("setCurrentSongListAndAlbums", data)
            } catch (error) {
                console.log(error)
            }
        },
        async loadAlbum(state, name) {
            try {
                console.log(name)
                const headers = { "Contet-Type": "application/json" };
                const body = JSON.stringify({ firstLoad: false, album: name });
                const server = await fetch("/loadAlbum", { method: "POST", body, headers, })
                const data = await server.json()
                state.commit("setCurrentSongListAndAlbums", data)
            } catch (error) {
                console.log(error)
            }
        },
        // async loadMusic(state, song) {
        //     try {
        //         console.log(name)
        //         const headers = { "Contet-Type": "audio/mp3" };
        //         const server = await fetch("/", { method: "GET", encodeURIComponent(song), headers })
        //         const data = await server.json()
        //     } catch (error) {
        //         console.log(error)
        //     }
        // },
    },
    mutations: {
        setCurrentSongListAndAlbums(state, data) {
            state.dirs = [];
            state.files = [];
            state.actualDir = data.actualDir;
            data.dirs.forEach(element => state.dirs.push(element));
            data.files.forEach(element => state.files.push(element));
            console.log(state)
        }
    }
})